.fav-btn {
  position: relative; }

.heart {
  position: absolute;
  left: auto;
  right: 50%;
  top: -50px;
  margin: auto;
  width: 2rem;
  height: 2rem;
  background: #c471ed;
  margin: 0.75rem;
  opacity: 0;
  max-width: 0;
  border-top-left-radius: 0.25rem;
  animation-name: grow;
  animation-duration: 0.4s;
  animation-iteration-count: infinite;
  transform: rotate(-135deg);
  transition: all 0.2s; }
  .heart:before, .heart:after {
    position: absolute;
    top: 0;
    content: '';
