  .heart:before {
    transform: translateX(50%); }
  .heart:after {
    transform: translateY(50%); }

@keyframes grow {
  0% {
    transform: scale(1) rotate(-135deg); }
  25% {
    transform: scale(1.25) rotate(-135deg); }
  50% {
    transform: scale(1) rotate(-135deg); } }

.heart.active {
  display: block;
  opacity: 1;
  max-width: 5rem; }

