iframe[name="google_conversion_frame"] {
	display: none;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}


/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
main,
section {
	display: block;
}

body {
	line-height: 1;
	-webkit-font-smoothing: antialiased;

}

ol,
ul ,li{
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

button {
	border: none;
	background: transparent;
	box-sizing: border-box;
}

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	/* 1 */
	font-size: 100%;
	/* 1 */
	line-height: 1;
	/* 1 */
	margin: 0;
	/* 2 */
	background: transparent
}

button {
	cursor: pointer;
}

button,
select {
	/* 1 */
	margin: 0;
	padding: 0;
	text-transform: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
	outline: 1px dotted ButtonText;
}

button:focus {
	outline: 0;
}

textarea {
	overflow: auto;
}

img,
svg {
	display: block;
	width: 100%;
	object-fit: cover;
	height: 100%;
}

input,
textarea {
	resize: none;
	outline: none;
	-webkit-border-radius: 0px;
	-webkit-appearance: none;
	border: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal;
}

a {
	text-decoration: none;
}

a {
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}

textarea::-webkit-input-placeholder,
input::-webkit-input-placeholder {
	/* WebKit browsers */
	color: inherit;
	font: inherit;
}

textarea:-moz-placeholder,
input:-moz-placeholder {
	color: inherit;
	font: inherit;
}

textarea::-moz-placeholder,
input::-moz-placeholder {
	color: inherit;
	font: inherit;
}

textarea:-ms-input-placeholder,
input:-ms-input-placeholder {
	color: inherit;
	font: inherit;
}

a {
	text-decoration: none;
	color: inherit
}

* {
	box-sizing: border-box;
}